.video {

    margin: 30px;
    border: 1px solid #ECECEC;
     @media (min-width: @tablet-width){
        margin: 40px 50px 60px;

     }
        @media (min-width: @desktop-width){
        margin: 57px 0 90px;

        display: flex;
        }
  &:hover{
    box-shadow:
    0 6px 10px 0 rgba(0, 0, 0, 0.1);
    }
  }

.video__title {
    position: relative;
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
    margin-top: 22px;
    margin-bottom: 26px;
    margin-left: 91px;
    @media (min-width: @tablet-width){

        font-size: 20px;
    }
    @media (min-width: @desktop-width){
        position: absolute;
        width: 179px;
        margin-top: 82px;
        left: 65%;
    }
    &::before {
      position: absolute;
      display: block;
      content: "";
      background-image:  url(../img/mobile/catalog/video-cam.svg);
      top: 0;
      width: 32px;
      height: 22px;

      @media (min-width: @desktop-width){
          top:-50px;
          left: 0;
      }
  }
}

.video__container{
    position: relative;
    display: block;
    width: 100%;
    height: 150px;
    background-image: url("../img/mobile/catalog/video-mobile.jpg");
    background-repeat: no-repeat;
    cursor: pointer;
    @media (min-width: @tablet-width){
        height: 455px;
        background-image: url("../img/desktop/catalog/video-desktop.jpg");
        order:-1;
    }
}
.video__play{
    position: absolute;
    margin-left: 80px;
    margin-top: 38px;
    @media (min-width: @tablet-width){
    margin-top: 189px;
    margin-left: 310px;
    }

    @media (min-width: @desktop-width){
      margin-left: 278px;
    }
    &:hover{
        scale: 1.5;

    }
    &:active{
        scale: 0.5;
        opacity: 0.3;
    }
}

.video__text-container{
    padding: 25px 36px 17px;

    @media (min-width: @desktop-width){
    padding: 146px 0 38px 63px;
    }
}
.video__info-text{
    margin-top: 8px;
    font-weight: 400;
    font-size: 13px;
    line-height: 24px;
    @media (min-width: @desktop-width){
        width: 253px;
        font-size: 15px;
        line-height: 30px;
    }
}


.video__button{
    display: block;
    width: 192px;
    padding: 16px 16px;
    margin: 40px auto 33px;
    @media (min-width: @tablet-width){
        width: 100%;
        text-align: center;

    }
    @media (min-width: @desktop-width){
        width: 230px;
        margin-top: 51px;
    }
}
