.contacts{
  position: relative;
  margin-top: 38px;


  @media (min-width:@tablet-width){
    margin-top: 67px;
  }
  @media (min-width:@desktop-width){
    margin-top: 71px;
    display: grid;
    grid-column: 1fr 1fr;
    column-gap: 30px;
    padding-right: 50px;
  }
  &::before {
  position: absolute;
  content: url("");
  background-image: url("../img/my-zig-zag-2-mobile.svg");
  background-repeat: repeat-x;
  background-size: 16px  6px;
  left: 0;
  right: 0;
  top: -30px;


  @media (min-width: @tablet-width){

  background-image: url("../img/my-zig-zag-2.svg");
  background-repeat: repeat-x;
  background-size: 39px 17px;
  top: -82px;

  }
  @media (min-width: @desktop-width){


      background-size: 53px 23px;
      top: -20px;
  }
  }
}

.contacts__title{
  position: relative;
  margin-top: 43px;
  margin-left: 30px;
  margin-bottom: 16px;
  @media (min-width:@tablet-width){
    margin-top: 163px;
    margin-bottom: 28px;
    margin-left: 0;
  }

  @media (min-width:@desktop-width){
    margin-left: 180px;
  }
  &::before {
    @media (min-width: @tablet-width){
    position: absolute;
    content: url("../img/tablet/flag.svg");
    width: 100%;
    right: 0;
    top: -30px;
  }
    @media (min-width: @desktop-width){
      top: 0;
    }
}
}

.card-contacts{
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  gap: 0;
  padding: 0 30px;
  max-width: 320px;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  @media (min-width:@tablet-width){
    font-size: 17px;
    padding: 0;
  }

  @media (min-width:@desktop-width){
    grid-template-columns: 1fr 3fr;
  }
}
.contacts__card-contacts{
  @media (min-width:@tablet-width){
   display: flex;


  }
  @media (min-width:@desktop-width){
    max-width: 320px;
    flex-direction: column;
    margin-left: 180px;
  }
}
.card-contacts__item{
  @media (min-width:@desktop-width){
  margin-top: 8px;
}
}
.contacts-adress{

  @media (min-width:@tablet-width){
  margin-left: 52px;
}
@media (min-width:@desktop-width){
  margin-left: 0;
}
}
.item-mail{
  text-decoration: none;
  color: inherit;
  border-bottom: 1px solid @green;
  padding-bottom: 3px;
  cursor: pointer;
}
.item-mail:hover{
  color: @green;
}
.item-mail:active {
  border: none;
}

.contacts__map{
  margin-right: 0;
  margin-top: 62px;
  @media (min-width:@tablet-width){
    width: 100%;
    margin-top: 29px;
  }

  @media (min-width:@desktop-width){
    width: 527px;
    height: 457px;
    grid-column: 2/3;
    grid-row: 1/4;
    margin-top: 82px;
    margin-right: 25px;
  }
}
.contacts__map iframe{
  display: block;
  width: 100%;
}
.card-contacts-link{
  display: block;
  margin: 30px auto 25px;
  padding: 15px 52px;
  align-self: center;
  @media (min-width:@tablet-width){
    margin-top: 60px;
    padding: 14px 37px;
    max-width: 230px;
  }

  @media (min-width:@desktop-width){
    margin-top: 39px;
    margin-left: 253px;
  }
}
